class Home extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
       data:'',
       fatched:false
    }
  }
  
  getDataFromApi(){
    httpGet('connectionService/getConnections').then(
      result =>
      this.setState({
        data:result,
        fatched:true
      })
      )
  }

  componentDidMount(){
    //this.getDataFromApi()
  }

  render() {
      return (
        <div style={{display:'flex',flexDirection:'row'}}>
          <Nav/>
          <div style={{display:'flex',flex:1,flexDirection:'row'}}>
          <div style={{width:'10%',height:'100vh',backgroundColor:'red'}}>
            <Sidenav/>
          </div>
          <div style={{display:'flex',justifyContent:'center',alignSelf:'center'}}>
          <div style={{width:'90%',marginTop:'10%'}}> 
          <Demo/>
          </div>
          </div>
          </div>
        </div>
      )
    }
}

export default Home
